import idx from 'idx';
import React from 'react';
import {graphql} from 'gatsby';
import {css} from '@emotion/core';
import {gradient, shadow} from '../../utils/theme';
import {rhythm} from '../../utils/typography';

const background = css`
  background-size: cover;
  background-position: center;
  color: white;
`;

const gradiantBackground = () => {
  return css`
    ${gradient}
    ${background}
  `;
};

const backgroundImage = props => css`
  background-image: url(${props.base64});
  background-image: url(${props.src});
  ${background}
`;

const container = css`
  padding: ${rhythm(2)} 0;
  margin-bottom: ${rhythm(1.5)};
  text-align: center;
  ${shadow}
`;

export default ({hero, children}) => {
  const img = idx(hero, _ => _.childImageSharp.fluid);

  return (
    <div css={[container, img ? backgroundImage(img) : gradiantBackground()]}>
      {children}
    </div>
  );
};

export const query = graphql`
  fragment Hero on frontmatter_2 {
    hero {
      childImageSharp {
        fluid(
          quality: 80
          maxWidth: 1920
          duotone: {highlight: "#ffcc5c", shadow: "#ff6f69", opacity: 50}
        ) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
